import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { newsStartLoading, newsClearActive } from '../../actions/noticia'
import TodasLasNoticias from './components/publicaciones/TodasLasNoticias'
import NuevaPublicacionModal from './components/publicaciones/NuevaPublicacionModal'
import { categoryStartLoading } from '../../actions/categoria'
import PaginacionAdmin from './components/publicaciones/PaginacionAdmin'


export const Publicaciones = () => {

    const { noticias, total } = useSelector(state => state.noticia)
    const { categoriaBD } = useSelector(state => state.categoria)

    const [paginaActual, setPaginaActual] = useState(1)
    const TOTAL_POR_PAGINA = 10

    const totalPag = Math.ceil(total / TOTAL_POR_PAGINA)


    const dispatch = useDispatch()

    let desde = (paginaActual - 1) * TOTAL_POR_PAGINA

    useEffect(() => {

        dispatch(newsStartLoading({ TOTAL_POR_PAGINA, desde }))
        dispatch(categoryStartLoading())
    }, [dispatch, desde])


    const [isOpenModal, setIsOpenModal] = useState(false)

    const closeModal = () => {
        if (isOpenModal) {
            dispatch(newsClearActive())
        }
        setIsOpenModal(!isOpenModal)
    }

    if (!categoriaBD) {
        return (<div className="lds-hourglass"></div>)
    }

    return (
        <div className='publicaciones'>

            <div className='publicaciones__btnNuevaPublicacion'>
                <button className='btn btn-primary' onClick={() => closeModal()}>Nueva Publicacin   </button>
            </div>

            <NuevaPublicacionModal
                isOpen={isOpenModal}
                close={closeModal}
            />

            <TodasLasNoticias className='publicaciones__lista-publicaciones'
                close={closeModal}
                noticias={noticias}
                categoria={categoriaBD}

            />
            <PaginacionAdmin paginaActual={paginaActual} totalPagina={totalPag} setPaginaActual={setPaginaActual} />
        </div>
    )
}
