const baseUrl = process.env.REACT_APP_API_URL

const fetchSinTokenParams = (endpoint, params) => {

    const { TOTAL_POR_PAGINA, desde } = params
    const url = `${baseUrl}/${endpoint}?limite=${TOTAL_POR_PAGINA}&desde=${desde}`

    return fetch(url)
}

const fetchSinTokenUrl = (endpoint, params) => {

    console.log(params)
    const url = `${baseUrl}/${endpoint}/${params}`
    return fetch(url)
}

const fetchSinToken = (endpoint, data, method = 'GET') => {

    const url = `${baseUrl}/${endpoint}`

    if (method === 'GET') {
        return fetch(url)

    } else {
        return fetch(url, {
            method,
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(data)
        })
    }

}
const fetchConToken = (endpoint, data, method = 'GET') => {

    const url = `${baseUrl}/${endpoint}`

    const token = localStorage.getItem('token') || '';

    if (method === 'GET') {
        return fetch(url, {
            method,
            headers: {
                'x-token': token
            }
        })

    } else {
        return fetch(url, {
            method,
            headers: {
                'Content-type': 'application/json',
                'x-token': token
            },
            body: JSON.stringify(data)
        })
    }

}





export {
    fetchSinToken,
    fetchConToken,
    fetchSinTokenParams,
    fetchSinTokenUrl
}