import { Nav, Navbar } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'


const Navigation = () => {
    return (

        <Navbar collapseOnSelect expand='lg' >
            {/* <Container className=' shadow-sm border border-start-0 border-end-0' > */}
            <div className='' >
                {/* <Navbar.Brand className='logo' style={{ display: 'none' }} >
                    <img className='img-fluid' src='images/logoNoticias.png' alt='LogoNoticias' />
                </Navbar.Brand> */}

                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">

                    <Nav className='menu'  >
                        <Nav.Link className="link-menu" as={NavLink} to='/' >INICIO</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/nacionales'>NACIONALES</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/internacionales'>INTERNACIONALES</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/policivas'>POLICIVAS</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/deportes'>DEPORTES</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/farandula'>FARANDULA</Nav.Link>
                        <Nav.Link className="link-menu" as={NavLink} to='/cienciaytecnologia'>CIENCIA Y TECNOLOGA</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </div >
        </Navbar >
    )
}

export default Navigation