import { Col, Row, } from 'react-bootstrap'

import { Link } from 'react-router-dom'
import { SidebarData } from '../../../components/admin/SidebarData'
const SideBarAdministrador = () => {

    return (
        <div className='sidebar'>
            <Row>

                {
                    SidebarData.map((val, key) => {
                        return (
                            <Col className='col-lg-12 col-sm-6' key={key}>
                                <Link
                                    className="sidebarList__row"
                                    to={val.link}
                                >
                                    <div className="sidebarList__valIcon">{val.icon}</div>
                                    <div className="sidebarList__vaLTitle">{val.title}</div>
                                </Link>
                            </Col>
                        )
                    })
                }
            </Row>
        </div>


    )
}

export default SideBarAdministrador