import React from 'react'
import { useSelector } from 'react-redux'

import 'moment/locale/es';
import { Link } from 'react-router-dom';


const UltimasNoticias = () => {

    const { newNews } = useSelector(state => state.nuevasNoticias)


    return (
        <div className='row modulo mb-5' >

            <div className='col-12 modulo__header'>
                <h3 className='modulo__titulo'>
                    Nuevas Noticias
                </h3>
            </div>

            {
                newNews.map(news => (

                    <div className='col-lg-4  col-sm-6 grid-margin mt-4 ' key={news._id}>
                        <div className=" position-relative image-hover">
                            <img
                                src={news.url}
                                className="img-fluid  img"
                                alt="world-news"
                            />
                        </div>
                        <span className='etiqueta-categria'>
                            <Link className='' to="/nacionales">{news.categoria.nombre}</Link>
                        </span>
                        <h2 className='noticia-titulo'>
                            <Link to={`/${news.categoria.nombre}/${news.urlNoticia}`} onClick={() => console.log('hola')}>
                                <p className="fs-15 font-weight-normal" dangerouslySetInnerHTML={{ __html: news.titulo }}></p>
                            </Link>
                        </h2>

                        {/* <p className="fs-15 font-weight-normal" dangerouslySetInnerHTML={{ __html: news.titulo.substring(0, 100) }}></p> */}
                    </div>

                ))
            }
        </div >



    )
}

export default UltimasNoticias