import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { vistas } from '../../helpers/vistas'


export const VistaNoticia = () => {
    const { noticiaId } = useParams()
    const { newNews } = useSelector(state => state.nuevasNoticias)
    const [noticia, setNoticia] = useState('')

    useEffect(() => {
        // const noticia = vistas(noticiaId, newNews)

        vistas(noticiaId, newNews)
            .then(noticia =>
                setNoticia(noticia)
            )

    }, [noticiaId, newNews])


    if (!noticia) {
        return (<h4>Espere....</h4>)
    }
    const { titulo, fecha, cuerpo, url } = noticia
    return (
        <article className='vista'>

            <h1 className='vista__titulo'>
                {titulo}
            </h1>
            <div className='vista__info'>
                <i className="bi bi-calendar4-week"></i>
                {fecha}
            </div>
            <div className=" position-relative ">
                <img
                    src={url}
                    className="img-fluid  img"
                    alt="world-news"
                />
            </div>
            <div className='vista__body mb-5'>
                <p className="fs-15 font-weight-normal" dangerouslySetInnerHTML={{ __html: cuerpo }}></p>
            </div>
        </article>
    )
}
