import { MenuTop } from '../../../pages/admin/components/MenuTop'
import SideBarAdministrador from '../../../pages/admin/components/SideBarAdministrador'

const AdminLayout = ({ children }) => {

    return (

        <div className='row grid-container'>

            <nav className='col-12 navbar-admin' > <MenuTop /></nav>
            <aside className='col-lg-2 col-sm-12 sidebar-admin'><SideBarAdministrador /></aside>

            <article className='col-lg-10  col-sm-12 main-admin'>
                {children}
            </article>
        </div>

    )
}

export default AdminLayout