import { types } from "../types/types"

const initialState = {
    categoriaBD: false
}



export const categoriaReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.categoryLoaded:
            return {
                ...state,
                categoriaBD: [...action.payload]
            }
        default:
            return state
    }
}