import React from 'react'
import { Button, Modal } from 'react-bootstrap'
import { AddEditPostForm } from './AddEditPostForm'

const NuevaPublicacionModal = ({ isOpen, close }) => {


    return (

        <Modal show={isOpen} onHide={close} size="xl" >
            <Modal.Header closeButton>
                <Modal.Title>
                    Nueva Publicacin
                </Modal.Title>
            </Modal.Header>

            <Modal.Body>

                <AddEditPostForm close={close} />

            </Modal.Body>

            <Modal.Footer>
                <Button variant='secondary' onClick={close} >Cerrar</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default NuevaPublicacionModal