import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { startLogin } from '../../actions/auth'
import { useForm } from '../../hooks/useForm'
import validator from 'validator'

const LoginScreen = () => {
    const dispatch = useDispatch()

    const [formValues, handleInputChange] = useForm({
        email: '',
        password: ''
    })
    const [msgError, setMsgError] = useState(false)
    const { email, password } = formValues

    const handleForm = (e) => {
        e.preventDefault()

        if (isValidForm()) {

            dispatch(startLogin(email, password))
        }
    }

    const isValidForm = () => {

        if (!validator.isEmail(email)) {
            setMsgError('El correo es invlido')
            return false
        }
        if (password.length < 6) {
            setMsgError('La contrasea debe tener al menos 6 caracteres')
            return false
        }
        setMsgError(false)
        return true
    }

    return (
        <div className='auth-main'>
            <div className='auth-main__container'>
                <h2 className='text-center mt-2'>Inicia sesin</h2>
                {
                    msgError &&
                    (
                        <div className='alert-error'>
                            {msgError}
                        </div>
                    )
                }
                <form onSubmit={handleForm} >
                    <div className='form-group mt-3'>
                        <label>Correo</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='email'
                            name='email'
                            value={email}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='form-group mt-3'>
                        <label>Contrasea</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='password'
                            name='password'
                            value={password}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='text-center mt-3'>
                        <button className='btn btn-primary'>Iniciar sesin</button>
                    </div>

                </form>

                <div className='text-center mt-2'>
                    <Link className='link' to='/auth/register' >Crear una cuenta</Link>
                </div>
            </div>
        </div >
    )
}

export default LoginScreen