import React from 'react'
import { Pagination } from 'react-bootstrap'
const PaginacionAdmin = ({ paginaActual, totalPagina, setPaginaActual }) => {


    const getpaginas = () => {
        const resultado = []

        for (let i = 0; i < totalPagina; i++) {
            let pagina = i + 1
            resultado.push(

                <Pagination.Item
                    active={pagina === paginaActual}

                    onClick={() => setPaginaActual(pagina)}
                    key={i}> {pagina} </Pagination.Item>)
        }

        return resultado
    }


    return (
        <div >
            {/* <span>Page {paginaActual} of {totalPagina}:</span> */}
            <Pagination className='justify-content-center' > {getpaginas()}</Pagination>
        </div>
    )
}

export default PaginacionAdmin