import React from 'react'

const RedesSociales = () => {
    return (
        <div className='box-left'>
            <div className='modulo__header mb-3'>
                <div className='modulo__titulo'>
                    Redes Sociales
                </div>
            </div>
            <div className='redes'>
                <div className='redes__twitter'>
                    <i className="bi bi-twitter"></i>

                    <span>69</span>

                </div>
                <div className='redes__instagram'>
                    <i className="bi bi-instagram"></i>
                    <span>69</span>
                </div>

                <div className='redes__facebook'>
                    <i className="bi bi-facebook"></i>
                    <span>69</span>
                </div>
            </div>
        </div>
    )
}

export default RedesSociales