import React from 'react'
import { Route, Routes } from 'react-router-dom'
import ContactanosScreen from '../pages/ContactanosScreen'

import InicioScreen from '../pages/InicioScreen'
import InternacionalesScreen from '../pages/InternacionalesScreen'
import NacionalesScreen from '../pages/NacionalesScreen'
import Layout from '../components/layouts/Layout'
import VistaNacionales from '../pages/vista/VistaNacionales'
import { PolicivasScreen } from '../pages/PolicivasScreen'
import { DeportesScreen } from '../pages/DeportesScreen'
import FarandulaScreen from '../pages/FarandulaScreen'
import CienciaYTecnologiaScreen from '../pages/CienciaYTecnologiaScreen'

const DashboardRoutes = () => {
    return (
        <Layout>
            <Routes>
                <Route path='/' element={<InicioScreen />} />
                <Route path='/nacionales' element={<NacionalesScreen />} />
                <Route path='/nacionales/:noticiaId' element={<VistaNacionales />} />

                <Route path='/internacionales' element={<InternacionalesScreen />} />
                <Route path='/internacionales/:noticiaId' element={<VistaNacionales />} />

                <Route path='/deportes' element={<DeportesScreen />} />
                <Route path='/deportes/:noticiaId' element={<VistaNacionales />} />

                <Route path='/policivas' element={<PolicivasScreen />} />
                <Route path='/policivas/:noticiaId' element={<VistaNacionales />} />

                <Route path='/farandula' element={<FarandulaScreen />} />
                <Route path='/farandula/:noticiaId' element={<VistaNacionales />} />

                <Route path='/cienciaytecnologia' element={<CienciaYTecnologiaScreen />} />
                <Route path='/cienciaytecnologia/:noticiaId' element={<VistaNacionales />} />

                <Route path='/contactanos' element={<ContactanosScreen />} />
            </Routes>
        </Layout>
    )
}

export default DashboardRoutes