import React from 'react'
import RedesSociales from '../../components/redes/RedesSociales'
import { MasVistoV } from '../components/MasVistoV'
import { VistaNoticia } from './VistaNoticia'

const VistaNacionales = () => {
    return (
        <div className='container mt-3'>
            <div className='row'>
                <div className='col-md-8'>
                    <VistaNoticia />
                </div>
                <div className='col-md-4'>
                    <RedesSociales />
                    <MasVistoV />
                </div>
            </div>
        </div>
    )
}

export default VistaNacionales