import { types } from "../types/types"


const initialState = {
    noticias: [],
    activa: null
}

export const noticiaReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.newsAddNew:
            return {
                ...state,
                noticias: [
                    action.payload,
                    ...state.noticias
                ]
            }
        case types.newsLoaded:
            return {
                ...state,
                noticias: [...action.payload[1]],
                total: action.payload[0]
            }
        case types.newsActiva:
            return {
                ...state,
                activa: action.payload
            }

        case types.newsClearActive:
            return {
                ...state,
                activa: null
            }

        case types.newsEdited:
            return {
                ...state,
                noticias: state.noticias.map(news => (
                    news._id === action.payload._id
                        ? action.payload
                        : news
                ))
            }
        case types.newsDelete:
            return {
                ...state,
                noticias: state.noticias.filter(news => (
                    news._id !== action.payload
                ))
            }

        default:
            return state
    }
}