import { types } from "../types/types"

const initialState = {
    newNews: []
}
export const nuevaNoticiaReducer = (state = initialState, action) => {

    switch (action.type) {
        case types.newNewsLoaded:
            return {
                ...state,
                newNews: [...action.payload]
            }
        default:
            return state
    }
}