import { fetchConToken, fetchSinTokenParams } from "../helpers/fetch"
import { types } from "../types/types"
import Swal from 'sweetalert2'
import { subirImagen } from '../helpers/subirImagen'

export const newsStartAddNew = ({ postData, urlFile }) => {
    return async (dispatch, getState) => {
        const { uid } = getState().auth

        // subir imagen
        const url = (urlFile)
            ? await subirImagen(urlFile)
            : false

        const noticia = {
            ...postData,
            url,
        }
        noticia.usuario = uid

        try {
            const resp = await fetchConToken('noticias', noticia, 'POST')
            const data = await resp.json()

            if (data.ok) {

                dispatch(newsAddNew(data.noticia))
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const newsAddNew = (noticia) => ({
    type: types.newsAddNew,
    payload: noticia
})

export const newsStartLoading = (params) => {
    return async (dispatch) => {
        try {
            const resp = await fetchSinTokenParams('buscar/noticias', params)
            const body = await resp.json()
            // const [total, noticias] = body.results

            // console.log(total, noticias)

            dispatch(newsLoaded(body.results))

        } catch (error) {
            console.log(error)
        }

    }
}

const newsLoaded = (noticias) => ({
    type: types.newsLoaded,
    payload: noticias
})

export const newSetActiva = (noticiaActiva) => ({
    type: types.newsActiva,
    payload: noticiaActiva
})

export const newsClearActive = () => ({ type: types.newsClearActive })


export const newsEdit = ({ postData, urlFile }) => {

    return async (dispatch) => {

        // subir la imagamen 
        const url = (urlFile)
            ? await subirImagen(urlFile)
            : postData.url

        const noticia = { // noticia editada para el Reduce
            ...postData,
            url
        }

        try {

            const resp = await fetchConToken(`noticias/${noticia._id}`, noticia, 'PUT')
            const body = await resp.json()
            if (body.ok) {

                dispatch(newsEdited(noticia))
            } else {
                Swal.fire('Error', body.msg, 'error')
                console.log('error al actualizar')
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const newsEdited = (news) => ({
    type: types.newsEdited,
    payload: news
})

export const newsStartDelete = (id) => {
    return async (dispatch) => {
        try {
            const resp = await fetchConToken(`noticias/${id}`, {}, 'DELETE')
            const body = await resp.json()

            if (body.ok) {
                dispatch(newsDelete(id))
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const newsDelete = (id) => ({
    type: types.newsDelete,
    payload: id
})
