import { fetchSinTokenUrl } from "./fetch"


export const vistas = async (urlNoticiaP = '', noticias) => {
    console.log('entro')
    const noticia = noticias.find(noticia =>
        noticia.urlNoticia === urlNoticiaP)

    if (!noticia) {
        const resp = await fetchSinTokenUrl('noticias', urlNoticiaP)
        const { noticia } = await resp.json()
        return noticia[0]
    }

    return noticia
}




