import React, { useEffect, useState } from 'react'
import { Editor } from '@tinymce/tinymce-react'
import { Button, Col, Form, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { newsStartAddNew, newsEdit } from '../../../../actions/noticia'

const initialNews = {
    titulo: '',
    cuerpo: ``,
    estado: false,
    categoria: '6257066e7bf1f08b6ff54e38',
    urlNoticia: ''

}

export const AddEditPostForm = ({ close }) => {
    const dispatch = useDispatch()

    const { activa: noticiaActiva } = useSelector(state => state.noticia)

    const { categoriaBD } = useSelector(state => state.categoria)

    const [postData, setPostData] = useState(initialNews)
    const [urlFile, setUrlFile] = useState(null)
    const [img64, setImg64] = useState('')
    const [msgError, setMsgError] = useState(false)

    const { titulo, estado, categoria, urlNoticia } = postData

    useEffect(() => {
        if (noticiaActiva) {
            console.log(noticiaActiva)
            setPostData(noticiaActiva)
        } else {
            setPostData(initialNews)
        }
    }, [noticiaActiva])

    const handleSubmit = async (e) => {
        e.preventDefault()

        if (isValidForm()) { // validando formulario

            if (noticiaActiva) { // Actualizar una noticia

                dispatch(newsEdit({ postData, urlFile }))
            } else {

                // Nueva noticia
                dispatch(newsStartAddNew({ postData, urlFile }))
            }
            close() // cierra el modal
        }
    }

    const isValidForm = () => {
        if (titulo.trim().length === 0) {
            setMsgError('El titulo es obligatorio')
            return false
        }
        if (urlNoticia.trim().length === 0) {
            setMsgError('El URL es obligatorio')
            return false
        }


        return true
    }

    const handleFileChange = async (e) => {
        const [file] = e.target.files
        console.log(file.size)
        const isValidSize = file.size < 500000

        if (!isValidSize) return alert('Imagen muy pesada')

        const isNameOfOneImageRegEx = /.(jpe?g|gif|png)$/i;
        const isValidType = isNameOfOneImageRegEx.test(file.name)

        if (!isValidType) return alert('Solo pudes subir imgenes')

        // convertir imagen en base64
        const reader = new FileReader()
        reader.readAsDataURL(file)

        reader.onloadend = () => {
            setImg64(reader.result)
        }

        setUrlFile(file)
    }

    const textUrl = (text) => {
        const url = text.replace(' ', '-')
        return url.toLowerCase()
    }


    return (

        <Form className='add-edit-post-form' onSubmit={handleSubmit}>
            <Form.Group className='add-edit-post-form__group'>

                {
                    msgError &&
                    <div className='alert-error'>{msgError}</div>
                }
                <Row>
                    <Col className='col-12 col-lg-6 '>
                        <Form.Label>Titulo</Form.Label>
                        <Form.Control
                            type='text'
                            name='titulo'
                            value={titulo}
                            placeholder='Titulo de la noticia'
                            onChange={e =>
                                setPostData({ ...postData, titulo: e.target.value })} />
                    </Col>
                    <Col className='col-12 col-lg-6 '>
                        <Form.Label>URL</Form.Label>
                        <Form.Control
                            type='text'
                            name='urlNoticia'
                            value={urlNoticia}
                            placeholder='URL de la noticia'
                            onChange={e =>
                                setPostData({ ...postData, urlNoticia: textUrl(e.target.value) })} />
                    </Col>

                </Row>

            </Form.Group >


            <Row>

                <Col>
                    <Form.Group className='add-edit-post-form__group'>

                        <Form.Label>Borrador</Form.Label>
                        <Form.Check
                            type='switch'
                            name='borrador'
                            checked={estado}
                            // value={estado}
                            onChange={e =>
                                setPostData({ ...postData, estado: e.target.checked })} />

                    </Form.Group >

                    <Form.Group className='mt-5'>

                        <Form.Label>Imagen</Form.Label>
                        <Form.Control type='file'
                            size='sm'
                            onChange={handleFileChange}
                            accept='.jpg, .jpeg, .png'

                        />

                    </Form.Group>
                </Col>

                <Col Col className='col-12 col-lg-6 '>
                    <Form.Group className='text-center mt-2'>
                        {
                            img64
                                ? <div>
                                    <img className='img-thumbnail' src={img64} alt='imagen'
                                        style={{
                                            // width: '300px',
                                            height: '180px',
                                            // objectFit: 'cover'
                                        }} />
                                </div>
                                : <div>
                                    <img className='img-thumbnail' src={postData.url} alt='imagen'
                                        style={{
                                            // width: '300px',
                                            height: '180px',
                                            // objectFit: 'cover'
                                        }} />
                                </div>
                        }
                    </Form.Group>
                </Col>

            </Row>

            <Form.Group className='add-edit-post-form__group'>

                <Form.Label>Categorias</Form.Label>
                <Form.Select
                    value={categoria}
                    name='categoria'
                    onChange={e =>
                        setPostData({ ...postData, categoria: e.target.value })}
                >
                    {
                        categoriaBD.map(c => (

                            <option key={c._id} value={c._id}>{c.nombre}</option>
                        ))
                    }

                </Form.Select>

            </Form.Group>

            <Form.Group className='add-edit-post-form__group'>

                <Editor
                    initialValue={(postData.cuerpo ? postData.cuerpo : '')}
                    //  onInit={(evt, editor) => editorRef.current = editor}
                    //  
                    init={{
                        height: 400,
                        menubar: false,
                        plugins: [
                            'advlist autolink lists link image charmap print preview anchor',
                            'searchreplace visualblocks code fullscreen',
                            'insertdatetime media table paste code help wordcount'
                        ],
                        toolbar: 'undo redo | formatselect | ' +
                            'bold italic backcolor | alignleft aligncenter ' +
                            'alignright alignjustify | bullist numlist outdent indent | ' +
                            'removeformat | help',
                        content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                    }}
                    // onChange={e =>
                    //     console.log(e.target.getContent())}
                    onBlur={e =>
                        setPostData({ ...postData, cuerpo: e.target.getContent() })}
                />

                <Button variant='primary' type='submit' className='btn-submit' >{noticiaActiva ? "Actualizar publicacin" : "Crear publicacin"}</Button>
            </Form.Group>
        </Form>

    )
}

