import { types } from "../types/types"

const initialState = {
    nuevaNoticia: false,
    editarNoticia: false
}

export const uiReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.btnNuevaNoticia:
            return {
                ...initialState,
                nuevaNoticia: true
            }
        case types.btnEditarNoticia:
            return {
                ...initialState,
                editarNoticia: true
            }

        default:
            return state
    }
}