import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { starRegister } from '../../actions/auth'
import { useForm } from '../../hooks/useForm'
import validator from 'validator'

const RegisterScreen = () => {
    const dispatch = useDispatch()

    const [formValues, handleInputChange] = useForm({
        name: '',
        email: '',
        password1: '',
        password2: ''
    })
    const { name, email, password1, password2 } = formValues
    const [msgError, setMsgError] = useState(false)

    const handleRegister = (e) => {
        e.preventDefault()


        if (isFormValid()) {
            dispatch(starRegister(email, password1, name))
        }
    }

    const isFormValid = () => {
        if (name.trim().length === 0) {
            setMsgError('El nombre es obligatorio')
            return false
        }
        if (!validator.isEmail(email)) {
            setMsgError('El correo es invlido')
            return false
        }

        if (password1 !== password2 || password1.length < 6) {
            setMsgError('La contrasea debe tener al menos 6 caracteres y coincidir entre s')
            return false
        }
        setMsgError(false)
        return true
    }

    return (
        <div className='auth-main'>
            <div className='auth-main__container'>
                <h2 className='text-center mt-2'>Registro</h2>

                {
                    msgError &&
                    (
                        <div className='alert-error'>
                            {msgError}
                        </div>
                    )
                }
                <form onSubmit={handleRegister} >
                    <div className='form-group'>
                        <label>Nombre</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='text'
                            name='name'
                            value={name}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='form-group mt-2'>
                        <label>Correo</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='email'
                            name='email'
                            value={email}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='form-group mt-2'>
                        <label>Contrasea</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='password'
                            name='password1'
                            value={password1}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='form-group mt-2'>
                        <label>Repetir contrasea</label>
                        <input
                            className='form-control'
                            autoComplete='off'
                            type='password'
                            name='password2'
                            value={password2}
                            onChange={handleInputChange}
                        />
                    </div>
                    <div className='text-center mt-3'>
                        <button
                            className='btn btn-primary'>Registrar</button>
                    </div>

                </form>

                <div className='text-center mt-2'>
                    <Link className='link' to='/auth/login' >Iniciar sesin</Link>
                </div>
            </div>
        </div >
    )
}

export default RegisterScreen