import { LogoImg } from "../logo/LogoImg";
import Navigation from "../Navigation";
import NavigationTop from "./NavigationTop";


const Navbar = () => {
    return (
        <div>
            <NavigationTop />

            <div className="container row navbar-header">

                <LogoImg />

                <div className="col-12 p-0 barra">
                    <Navigation />
                </div>
            </div>
        </div>



    )
}

export default Navbar