
import { Table } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import Swal from 'sweetalert2'
import { newSetActiva, newsStartDelete } from '../../../../actions/noticia'
import { buscarCategoria } from '../../../../helpers/buscarCategoria'
import moment from 'moment'
import 'moment/locale/es';
import { Link } from 'react-router-dom'

const ListaNoticias = ({ close, noticias, categoria }) => {
    const dispatch = useDispatch()



    const noticiaEditar = (noticia) => {
        dispatch(newSetActiva(noticia))
        close()
    }
    const noticiaVer = (noticia) => {
        dispatch(newSetActiva(noticia))

    }

    const noticiaBorrar = (id) => {
        Swal.fire({
            title: 'Ests seguro?',
            text: "No podrs revetir esto!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, Borrar la publicacin!'
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire(
                    'Borrada!',
                    'La publicacin ha sido borrada.',
                )
                dispatch(newsStartDelete(id))
            }
        })
    }


    return (


        <Table responsive striped bordered hover className='publicaciones-tabla'>
            <thead >
                <tr>
                    <th>Titulo</th>
                    <th>Fecha</th>
                    <th>Categoria</th>
                    <th>Autor</th>
                    <th>Estado</th>
                    <th>Ver</th>
                    <th>Editar</th>
                    <th>Borrar</th>
                </tr>
            </thead>
            <tbody>

                {
                    noticias.map(noticia => (
                        <tr key={noticia._id}>
                            <td>{noticia.titulo} </td>
                            <td>{moment(noticia.fecha).format("LL")} </td>
                            <td>{buscarCategoria(noticia.categoria, categoria)}</td>
                            <td>{noticia.usuario}</td>
                            <td>
                                {
                                    noticia.estado
                                        ? 'Activa'
                                        : 'Borrador'
                                }

                            </td>
                            <td>
                                <Link to={`/${buscarCategoria(noticia.categoria, categoria)}/${noticia.urlNoticia}`}>
                                    <button
                                        className='btn btn-danger'
                                        onClick={() => noticiaVer(noticia)}>

                                        <i className="bi bi-eye"></i>
                                    </button>
                                </Link>
                            </td>

                            <td><button
                                className='btn btn-primary'
                                onClick={() => noticiaEditar(noticia)}
                                to='/'
                            > <i className="bi bi-pencil"></i></button> </td>
                            <td><button
                                className='btn btn-success'
                                onClick={() => noticiaBorrar(noticia._id)}
                            > <i className="bi bi-trash3"></i></button> </td>
                        </tr>
                    ))
                }
            </tbody>

        </Table>


    )
}

export default ListaNoticias